/* page */
/* Header page */
.hero-page {
  margin-bottom: 30vh;
  position: relative; }
  @media (max-width: 767px) {
    .hero-page {
      margin-bottom: 90px; } }

.hero-page::before {
  content: '';
  position: absolute;
  background-image: url(../images/image-2.png), url(../images/image-1.png);
  background-repeat: no-repeat;
  background-size: contain;
  z-index: -1;
  width: 100%;
  height: 100%; }
  @media (max-width: 767px) {
    .hero-page::before {
      background-size: cover; } }

.hero-page__connect::before {
  background-image: url(../images/image-3.png); }

.header-page {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 42px;
  padding-bottom: 34px; }
  @media (max-width: 767px) {
    .header-page {
      flex-direction: column; } }

@media (max-width: 767px) {
  .header-page__col:first-child {
    margin-bottom: 24px; } }

.site-logo {
  display: inline-block;
  width: 178px;
  height: 20px; }
  .site-logo img {
    max-width: 100%;
    max-height: 100%; }

.header-page__btns {
  display: flex;
  align-items: center; }
  @media (max-width: 767px) {
    .header-page__btns {
      flex-direction: column; } }

.header-page__btn {
  display: flex;
  align-items: center; }
  .header-page__btn svg {
    flex-shrink: 0;
    margin-right: 15px; }
  .header-page__btn strong {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px; }

.header-page__btn:last-child {
  background: linear-gradient(180deg, #EB2655 0%, #72021D 100%);
  border-radius: 10px;
  min-height: 50px;
  padding-right: 15px;
  padding-left: 15px;
  margin-left: 30px;
  transition: all 0.3s; }
  .header-page__btn:last-child :hover {
    cursor: pointer; }
  @media (max-width: 767px) {
    .header-page__btn:last-child {
      margin-left: 0;
      margin-top: 10px; } }

/* Hero page */
.hero-page__inner {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  @media (max-width: 1199px) {
    .hero-page__inner {
      flex-direction: column-reverse; } }

@media (max-width: 767px) {
  .hero-page__connect {
    margin-bottom: 20px; } }

@media (max-width: 767px) {
  .hero-page__connect .hero-page__head {
    margin-bottom: 0; } }

.hero-page__connect .hero-page__col {
  width: 50%; }
  @media (max-width: 1199px) {
    .hero-page__connect .hero-page__col {
      width: 100%; } }

@media (max-width: 1199px) {
  .hero-page__col:last-child {
    width: 60%; } }

@media (max-width: 767px) {
