.hero-page__btn {
  background: linear-gradient(180deg, #EB2655 0%, #72021D 100%);
  border-radius: 10px;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  min-height: 50px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: 30px;
  display: flex;
