  color: #FFFFFF;
  text-decoration: none;
  max-width: 245px;
  width: 100%; }
  @media (max-width: 767px) {
    .hero-page__btn {
      margin-right: 0; } }

.hero-page__info img {
  max-width: 100%;
  max-height: 100%; }

