  font-weight: 400; }

.hero-page__info-img {
  width: 40px;
  height: 40px;
  margin-right: 20px; }

.casino-img {
  max-width: 100%;
  width: 100%;
  height: auto; }
  @media (max-width: 767px) {
    .casino-img {
      width: 314px;
      margin: 0 auto; } }

.casino-img_connect {
  width: 595px;
  margin-left: auto; }
  @media (max-width: 767px) {
    .casino-img_connect {
      width: 223px; } }

.hero-page__offer {
  background: #050A1C;
  border-radius: 10px;
  padding: 35px 56px;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
  text-align: center;
  width: 100%; }
  .hero-page__offer .hero-page__offer-link {
    text-decoration: none;
    color: #EB2655; }
  @media (max-width: 1199px) {
    .hero-page__offer {
      margin-top: 30px; } }
  @media (max-width: 767px) {
    .hero-page__offer {
      font-size: 14px;
      line-height: 24px;
      padding: 10px 15px; } }

.main-page {
  position: relative;
  min-height: 65vh; }
  @media (max-width: 1199px) {
    .main-page {
      margin-bottom: 30px; } }

.main-page__active::before {
  display: none; }

.main-page::before {
  content: "";
  position: absolute;
  top: -20vh;
  left: 50%;
  margin-left: -30%;
  background-image: url(../images/image-3.png);
  background-repeat: no-repeat;
  background-size: contain;
  z-index: -1;
  width: 100%;
  height: 100%; }

.cards {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: -30px;
  position: relative; }
  @media (max-width: 1199px) {
    .cards {
      flex-wrap: wrap;
      align-items: flex-start;
      margin-bottom: -70px; } }
  @media (max-width: 767px) {
    .cards {
      align-items: center;
      justify-content: center;
      margin-right: 0; } }

.cards::before {
  content: "";
  position: absolute;
  top: -140px;
  left: -140px;
  background-image: url(../images/coin.png);
  background-size: contain;
  background-repeat: no-repeat;
  z-index: 1;
  width: 240px;
  height: 240px; }
  @media (max-width: 1199px) {
    .cards::before {
      display: none; } }

.card {
  background: linear-gradient(180deg, #111A36 0%, #04091B 100%);
  border-radius: 10px;
  padding: 79px 20px 53px;
  text-align: center;
  min-height: 241px;
  margin-right: 30px;
  position: relative;
  flex: 0 0 calc((100% - 3*30px)/3);
  max-width: calc((100% - 3*30px)/3); }
  @media (max-width: 1199px) {
    .card {
      flex: 0 0 calc((100% - 2*30px)/2);
      max-width: calc((100% - 2*30px)/2);
      margin-bottom: 70px; } }
  @media (max-width: 767px) {
    .card {
      flex: 0 0 100%;
      max-width: 100%;
      margin-right: 0; } }

.card::before {
  content: '';
  position: absolute;
  top: -50px;
  margin-left: -55px;
  background-image: url(../images/icon-1.png);
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: cover;
  width: 111px;
  height: 99px; }

.card__bonus::before {
  background-image: url(../images/icon-2.png); }

.card__game::before {
  background-image: url(../images/icon-3.png); }

.card__title {
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
  margin-bottom: 20px;
  white-space: nowrap; }
  @media (max-width: 1199px) {
    .card__title {
      white-space: unset; } }

.card__subtitle {
  font-size: 16px;
  line-height: 20px;
  font-weight: 400; }

.main-page__btn {
  margin: 30px auto 0; }

/* grid-item*/
.main-page__connect {
  margin-bottom: 50px; }

.grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 30px; }
  @media (max-width: 1199px) {
    .grid {
      grid-template-columns: repeat(3, 1fr); } }
  @media (max-width: 991px) {
    .grid {
      grid-template-columns: repeat(2, 1fr); } }

.grid__item {
  background: #C4C4C4;
  border-radius: 10px;
  width: 100%;
  min-height: 332px;
  display: block; }
  @media (max-width: 767px) {
    .grid__item {
      min-height: 250px; } }

.grid__item img {
  display: block;
  border-radius: 10px; }

.grid__item-row {
  grid-column: span 2;
  grid-row: span 2; }
  @media (max-width: 991px) {
    .grid__item-row {
      grid-column: initial;
      grid-row: initial; } }

